import styled from 'styled-components'
import Header from '../components/Header'

const LayoutStyle = styled.div`
  margin: 20px;
  padding: 20px;
  border: 1px solid #ddd;
`

const TitleComponent = ({ className, node }) => <h1 className={className}>{node}</h1>

const Title = styled(TitleComponent)`
  font-size: 14px;
  font-weight: bold;
`

const Layout = (props) => (
  <LayoutStyle>
    <Header />
    <Title node={props.children} />
  </LayoutStyle>
)

export default Layout


